.intro {
  display: flex;
  height: 100vh;
}

.intro-left {
  flex: 1;
}

.intro-right {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.intro-left-wrapper {
  display: flex;
  flex-direction: column;
  padding: 25%;
  margin-left: 50px;
}

.intro-start {
  font-size: 30px;
  font-weight: 300;
}

.intro-name {
  font-size: 50px;
}

.intro-title {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #59b256;
  height: 100%;
}

.intro-image {
  height: 70%;
}
